import { forwardRef, useState } from "react";
import { Table } from "react-bootstrap";
import WeeklyForm from "./WeeklyForm";
import DatePicker from "react-datepicker";
import "./scrollbar.css";
import "./boxstyle.css";

function Weekly(props) {
  // // test
  // const click = (item) =>{
  //   window.open("/home","test","width=500,height=600");
  // }
  const [startDate, setStartDate] = useState(new Date());
  const ExampleCustomInput = forwardRef(({ value, onClick }, ref) => (
    <button className="example-custom-input" onClick={onClick} ref={ref}>
      {value}
    </button>
  ));
  return(
    <>
    <h5>WEEKLY <img src="/weekly.png"width="30"height="30"/>
    <div className="text-center">
    <DatePicker
      selected={startDate}
      onChange={(date) => setStartDate(date)}
      customInput={<ExampleCustomInput />}
    />
    </div>
    </h5>
    <div className="scrollbar" id="style-7">
      <div className="force-overflow-auto">
      <Table bordered>
  <thead>
    <tr className="text-center" style={{backgroundColor:"rgb(18, 60, 114)", color:"white"}}>
      <th><img src="/speed.png"width="20"height="20"/></th>
      <th></th>
      <th></th>
      <th></th>
      <th></th>
      <th></th>
      <th></th>
      <th></th>
    </tr>
  </thead>
  <tbody>
    <tr className="text-center">
      <td style={{height:"500px"}}><WeeklyForm/></td>
      <td style={{width:"150px"}}>
        <div className="banner1"> <div style={{float: "inline-end"}}><hr></hr>   09:00 ~ 11:00</div></div>
      </td>
      <td style={{width:"150px"}}>
      <WeeklyForm/>
      </td>
      <td style={{width:"150px"}}>
      <div className="banner2"> <div style={{float: "inline-end"}}><hr></hr>   09:00 ~ 11:00</div></div>
      <div className="banner1"> <div style={{float: "inline-end"}}><hr></hr>   13:00 ~ 15:00</div></div>
      </td>
      <td style={{width:"150px"}}>
      <WeeklyForm/>
      </td>
      <td style={{width:"150px"}}>
      <div className="banner3"> <div style={{float: "inline-end"}}><hr></hr>   09:00 ~ 11:00</div></div>
      </td>
      <td style={{width:"150px"}}>
      <WeeklyForm/>
      </td>
      <td style={{width:"150px"}}>
      <div className="banner2"> <div style={{float: "inline-end"}}><hr></hr> 13:00 ~ 18:00</div></div>
      </td>
    </tr>
  </tbody>
</Table>
</div>
</div>
    </>
  )
}

export default Weekly;